import { Component, Input, OnInit, ViewChild, ViewChildren } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { isObject } from 'rxjs/internal/util/isObject';
import { FormModel } from '../../../../shared/model/formsModel';
import { stepTwoSupplier } from '../forms/step-two-supplier-form';
import { stepTwoRetailer } from '../forms/step-two-retailer-form';
import { AddProductService } from '../../../../services/add-product/add-product.service';
import { UploadImagesComponent } from '../upload-images/upload-images.component';
import { AttributesListComponent } from '../attributes-list/attributes-list.component';

@Component({
  selector: 'step-two',
  templateUrl: './step-two.component.html',
  styleUrls: ['./step-two.component.scss']
})

export class StepTwoComponent implements OnInit {
  @ViewChildren('uploadImages') uploadImages: UploadImagesComponent;
  @ViewChild('attributesList') attributesList: AttributesListComponent;

  @Input() productId: any;
  @Input() seller_id: any;

  public formsList: FormModel[] = [];
  public colorsList: any[] = [];
  public colorsForSelectionLength: number;
  public stepTwoForm: FormGroup;

  constructor(
    private _Router: Router,
    private _AddProductService: AddProductService
  ) {
    this.stepTwoForm = new FormGroup({});
  }

  ngOnInit(): void {
    if (this._Router.url.includes('/products/sellers')) {
      this.formsList = stepTwoSupplier;
    }

    if (this._Router.url.includes('/products/stores')) {
      this.formsList = stepTwoRetailer;
    }

    this.formsList?.forEach(form => {
      this.stepTwoForm.addControl(form.formControl, new FormControl('', form.validators));
      if (form.autocompleteList) this['get' + form.label]()
      if (form.selectList) this['get' + form.label]()
    });
  }

  getFormData(): FormData[] {
    let formData: FormData[] = [];
    this.colorsList.forEach(colorItem => {
      let newFormData: FormData = new FormData();
      newFormData.append('product_id', this.productId);
      newFormData.append('seller_id', this.seller_id);
      newFormData.append('color_id', colorItem?.color_id?.id);
      newFormData.append('barcode', colorItem?.barcode);
      newFormData.append('quantity', colorItem?.quantity);
      colorItem?.images?.forEach((image: any, index: number) => {
        newFormData.append('images[' + index + ']', image?.file);
      });
      formData.push(newFormData);
    });
    return formData;
  }

  validate() {
    if (this.colorsList?.length === 0) return true;
    return false;
  }

  getColor() {
    this._AddProductService.getColors().subscribe(
      (res: any) => {
        this.colorsForSelectionLength = res['data'].length;
        this.formsList.filter(elem => {
          if (elem.label == 'Color') {
            elem['selectList'] = res.data;
          }
          return elem;
        })
      },
      error => {
      },
      () => {
      },
    )
  }

  addToList() {
    let color = this.stepTwoForm.value;
    this.colorsList.push(color);
    this.resetForm();
  }

  updateColorsList(event) {
    this.colorsList.splice(event.index, 1);
  }

  validateAddToList() {
    if (!this.stepTwoForm.valid) return true;
    if (this.colorsList.some(color => color?.color_id.id === this.stepTwoForm.value['color_id'].id)) return true;

    return false;
  }

  getSizesUpdates(sizes) {
    this.stepTwoForm.controls['sizes'].setValue(sizes);
  }

  getImageUpdates(newImagesList) {
    if (newImagesList?.length > 0) this.stepTwoForm.controls['images'].setValue(newImagesList);
  }

  resetForm() {
    this.stepTwoForm.reset();
    this.uploadImages['_results'][0].reset();
    if (this.attributesList) this.attributesList.attrList = [];
  }

  viewHandle(value: any) {
    if (isObject(value)) {
      return value.name ? value.name : value.name_en;
    } else {
      return value;
    }
  }
}