import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormControl } from '@angular/forms';

@Component({
  selector: 'upload-images',
  templateUrl: './upload-images.component.html',
  styleUrls: ['./upload-images.component.scss']
})
export class UploadImagesComponent implements OnInit {

  @Output() imageUpdates: EventEmitter<any> = new EventEmitter();

  public fileSelected: File;
  public imageList: { file: File, src: any }[] = [];
  public defaultImgLink: string = "/assets/icons/picture.svg";
  public currentImage: any = this.defaultImgLink;
  public currentImagePath: any = this.defaultImgLink;

  constructor() { }

  ngOnInit(): void {
  }

  public handleFileInput(event) {
    let files = event.target?.files;
    this.addNewImg(files[0]);
  }

  public reset() {
    this.imageList = [];
    this.currentImage = this.defaultImgLink;
    this.getCurrentImagePath(this.currentImage);
  }

  public addNewImg(newImage: any) {
    if (this.imageList?.some((img: any) => img === newImage)) {
      // this._toasterService.show('Image Already Selected', 'Error', { status: 'danger' });
    } else {
      let reader = new FileReader();
      this.currentImage = newImage;
      reader.readAsDataURL(newImage);
      reader.onload = (_event) => {
        this.imageList.push({ file: newImage, src: reader.result });
      }
      this.getCurrentImagePath(this.currentImage);
      this.imageUpdates.emit(this.imageList);
    }
  }

  public getCurrentImagePath(currentImage: any): any {
    if (currentImage === this.defaultImgLink) {
      this.currentImagePath = this.defaultImgLink;
    } else {
      let reader = new FileReader();
      reader.readAsDataURL(currentImage);
      reader.onload = (_event) => {
        this.currentImagePath = reader.result;
      }
    }

  }

  public changeMainImg(selectedImage: any) {
    this.currentImage = selectedImage;
  }

  public removeImage(deleteImage: any) {
    this.imageList = this.imageList.filter(img => img?.src !== deleteImage?.src);
    if (this.currentImagePath === deleteImage?.src) {
      this.currentImage = this.imageList[0] ? this.imageList[0].file : this.defaultImgLink;
    }
    this.getCurrentImagePath(this.currentImage);
    this.imageUpdates.emit(this.imageList);
    // this.getAllImagePath();
  }
}