import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { isObject } from 'rxjs/internal/util/isObject';
import { FormModel } from '../../../../shared/model/formsModel';
import { stepOneSupplier } from '../forms/step-one-supplier-form';
import { stepOneRetailer } from '../forms/step-one-retailer-form';
import { distinctUntilChanged, debounceTime } from 'rxjs/operators';
import { CategoriseServices } from '../../../../services/categories/categories.services';
import { Observable } from 'rxjs';
import { BrandsServices } from '../../../../services/brands/brands.services';
import { StoresServices } from '../../../../services/stores/stores.services';
import { MaterialsServices } from '../../../../services/materials/materials.services';
import { AttributesListComponent } from '../attributes-list/attributes-list.component';
import { PoliciesServices } from '../../../../services/policies/policies.services';
import { ShippingMethodsServices } from '../../../../services/shipping-methods/shipping-methods.services';

@Component({
  selector: 'step-one',
  templateUrl: './step-one.component.html',
  styleUrls: ['./step-one.component.scss']
})
export class StepOneComponent implements OnInit {
  @ViewChild('attributesList') attributesList: AttributesListComponent;
  public formsList: FormModel[] = [];
  public stepOneForm: FormGroup;
  type;
  constructor(
    private _Router: Router,
    private _CategoriseServices: CategoriseServices,
    private _BrandsServices: BrandsServices,
    private _StoresServices: StoresServices,
    private _MaterialsServices: MaterialsServices,
    private _PoliciesServices: PoliciesServices,
    private _ShippingMethodsServices: ShippingMethodsServices,
  ) {
    this.stepOneForm = new FormGroup({});
  }

  ngOnInit(): void {
    if (this._Router.url.includes('/products/sellers')) {
      this.type = 2;
      this.formsList = stepOneSupplier;
    }

    if (this._Router.url.includes('/products/stores')) {
      this.type = 1;
      this.formsList = stepOneRetailer;
    }

    this.formsList?.forEach(form => {
      this.stepOneForm.addControl(form.formControl, new FormControl('', form.validators));
      if (form.type === 'auto') {
        this.stepOneForm.get(form.formControl).valueChanges.pipe(distinctUntilChanged(), debounceTime(800)).subscribe({
          next: (value) => {
            if (!isObject(value)) {
              this.getAutoData(form, value);
            }
          }
        })
      }
      if (form.formControl === 'category_id') this.fireAutocompleteApi(this._CategoriseServices.getCategoriesByLevel("3",), form);
      if (form.formControl === 'brand_id') this.fireAutocompleteApi(this._BrandsServices.getBrands(), form);
      if (form.formControl === 'store_id') this.fireAutocompleteApi(this._StoresServices.getAllStores('type=' + this.type), form);
      if (form.formControl === 'material_id') this.fireAutocompleteApi(this._MaterialsServices.getMaterials(), form);
      if (form.formControl === 'policy_id') this.fireAutocompleteApi(this._PoliciesServices.getPolicies(), form);
      if (form.formControl === 'shipping_method_id') this.fireAutocompleteApi(this._ShippingMethodsServices.getShippingMethods(), form);
    });
  }

  private getAutoData(formControl: FormModel, value: string) {
    let apiSubscriper: Observable<any>;
    switch (formControl.formControl) {
      case 'category_id':
        apiSubscriper = this._CategoriseServices.getCategoriesByLevel("3", 'name=' + value);
        break;

      case 'brand_id':
        apiSubscriper = this._BrandsServices.getBrands('name=' + value);
        break;

      case 'store_id':
        apiSubscriper = this._StoresServices.getAllStores('name=' + value + '&type=' + this.type);
        break;

      case 'material_id':
        apiSubscriper = this._MaterialsServices.getMaterials('name=' + value);
        break;

      case 'policy_id':
        apiSubscriper = this._PoliciesServices.getPolicies('name=' + value);
        break;
      case 'shipping_method_id':
        apiSubscriper = this._ShippingMethodsServices.getShippingMethods('name=' + value);
        break;
    }
    this.fireAutocompleteApi(apiSubscriper, formControl);
  }

  private fireAutocompleteApi(apiSubscriper: Observable<any>, formControl: FormModel) {
    apiSubscriper?.subscribe({
      next: (res) => {
        this.formsList.find(form => form.id === formControl.id).autocompleteList = res.data;
      },
      error: (error) => { },
      complete: () => { },
    })
  }

  public checboxToggle(check: boolean, control: FormModel) {
    this.stepOneForm.get(control.formControl).setValue(check);
  }

  getFormData() {
    return this.stepOneForm.value;
  }

  viewHandle(value: any) {
    if (isObject(value)) {
      return value.name ? value.name : value.name_en;
    } else {
      return value;
    }
  }

}
