import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { ApiModel } from '../../shared/model/api.model';

@Injectable({
  providedIn: 'root'
})
export class AddProductService {

  constructor(private http: HttpClient) { }

  public getColors() {
    const url = environment.MAIN_SYSTEM + 'api/dashboard/colors/get-all/for-selection';
    return this.http.get(url);
  }

  public addStepOneSupplier(body: any) {
    const url = environment.MAIN_SYSTEM + 'api/dashboard/productsV2/create-product';
    return this.http.post(url, body);
  }

  public addStepTwoSupplier(body) {
    const url = environment.MAIN_SYSTEM + 'api/dashboard/productsV2/create-product-step2';
    // const url = environment.MAIN_SYSTEM + 'api/dashboard/products/create/supplier-step2';
    return this.http.post(url, body);
  }

  public addStepOneRetailer(body) {
    const url = environment.MAIN_SYSTEM + 'api/dashboard/productsV2/create-retail-product';
    return this.http.post(url, body);
  }

  public addStepTwoRetailer(body) {
    const url = environment.MAIN_SYSTEM + 'api/dashboard/products/create/retailer-step2';
    return this.http.post(url, body);
  }
}
