import { DatePipe } from '@angular/common';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { NbStepperComponent, NbToastrService } from '@nebular/theme';
import { AddProductService } from '../../../../services/add-product/add-product.service';
import { StepOneComponent } from '../step-one/step-one.component';
import { StepTwoComponent } from '../step-two/step-two.component';

@Component({
  selector: 'app-index-add-product',
  templateUrl: './index-add-product.component.html',
  styleUrls: ['./index-add-product.component.scss']
})
export class IndexAddProductComponent implements OnInit {
  @ViewChild('NbStepper') NbStepper: NbStepperComponent;
  @ViewChild('stepOne', { static: true }) stepOne: StepOneComponent;
  @ViewChild('stepTwo', { static: true }) stepTwo: StepTwoComponent;
  public loading: boolean = false;
  formType: 'Supplier' | 'Retailer';
  public productId: any;
  public seller_id: any;

  constructor(
    private _AddProductService: AddProductService,
    private _Router: Router,
    private _NbToastrService: NbToastrService,
    private _DatePipe: DatePipe,
    private toaster: NbToastrService,
  ) { }

  ngOnInit(): void {

    if (this._Router.url.includes('/products/sellers')) {
      this.formType = 'Supplier';
    }

    if (this._Router.url.includes('/products/stores')) {
      this.formType = 'Retailer';
    }
  }

  saveStepOne() {
    this.loading = true;
    let data = this.stepOne.getFormData();
    data.seller_id = data.store_id.user_id;
    this.seller_id = data.seller_id;
    data.brand_id = data.brand_id.id;
    data.category_id = data.category_id.id;
    data.store_id = data.store_id.id;
    data.material_id = data?.material_id?.id;
    data.policy_id = data?.policy_id?.id;
    data.shipping_method_id = data?.shipping_method_id?.id;
    data.publish_app_at = this._DatePipe.transform(data.publish_app_at, 'yyyy-MM-dd');
    data.product_attributes = this.stepOne?.attributesList?.attrList.map(ele => { return { size_id: ele.size.id, quantity: ele.quantity } })
    this._AddProductService['addStepOne' + this.formType](data).subscribe(
      (res: any) => {
        this.productId = res?.data?.id;
        this.toaster.show(res['msg'], 'Success', { status: 'success' });
        // this._Router.navigateByUrl('/brands');
      },
      (error: any) => {
        this.loading = false;
        this._NbToastrService.show(error?.error?.message, 'Error', { status: 'danger' });
      },
      () => {
        this.loading = false;
        this.NbStepper.next();
      },
    );
  }

  saveStepTwo() {
    this.loading = true;
    this.stepTwo.getFormData().forEach((data, index) => {
      this.saveProductColor(data, (index == this.stepTwo.getFormData().length - 1) ? true : false)
    });
  }

  saveProductColor(data: any, last: boolean) {
    // data.seller_id = this.seller_id;
    console.log(this.seller_id);
    if(last)
      data.append("reviewed",last?"1":"0");

    // console.log(data.);
    this._AddProductService['addStepTwo' + this.formType](data).subscribe(
      (res: any) => {
      },
      (error: any) => {
        this.loading = false;
        this._NbToastrService.show(error?.error?.message, 'Error', { status: 'danger' });
      },
      () => {
        if (last) {
          this.loading = false;
          this._NbToastrService.show('Product Colors are saved successfully', 'Success', { status: 'success' });
          this._Router.navigate(['/products/' + (this.formType == 'Supplier' ? 'sellers' : 'stores')])
        }
      },
    )
  }
}